.search_bg {
  background-color: rgb(81, 78, 78);
  height: 40px;
  width: 100%;
}
.search_main {
  display: flex;
  padding: 5px 0px;
  width: 55%;
  margin: auto;
  align-items: center;
}
.search_main > input {
  height: 20px;
  width: 100%;
  background: transparent;
  border: none;
  border-bottom: 2px solid white;
  color: white;
  padding: 12px 0px;
  z-index: 1;
  cursor: pointer;
}

::placeholder {
  color: white;
  padding-left: 30px;
}

.search_icon {
  width: 20px;
  margin-left: -30px;
}
.nav_main{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    padding: 20px 0px;
}

.nav_list{
  margin-right: 60px;
}

ul{
    display: flex;
    justify-content: space-between;
    list-style: none;
}

ul li{
    margin: 0 25px;
}

.head_btn{
  background-color: black;
  color: white;
  padding: 5px;
}

.logo{
  width: 120px;
}

.header_img{
  width: 100%;
}

.options{
  display: flex;
  align-items: center;
  width: 100%;
  align-items: center;
}

.options>div{
  width: 100%;
  padding: 10px 0px 10px 10px;
  border: 0.5px solid black;
  margin-top: -5px;
}